.catalog-event {
	.catalog-wrapper {
		overflow     : hidden;
		padding      : 50px 0;
		border-bottom: 1px solid #323637;

		.title-catalog {
			display    : flex;
			align-items: center;
			padding    : 0 30px;

			.name-catalog {
				margin-left: 10px;

				span {
					font-family: 'Ubuntu', sans-serif;
					font-size  : 12px;
					font-weight: 700;
					color      : rgb(160, 160, 160);
				}

				h3 {
					font-family    : 'Saira Stencil One', cursive;
					font-size      : 24px;
					line-height    : 1.2;
					color          : $main-color-1;
					text-decoration: underline;
				}
			}

			.event-tag {
				margin-left: auto;

				.list-eventTag {
					display    : flex;
					align-items: center;

					.item {
						padding      : 3px 0;
						margin-right : 10px;
						background   : rgba(255, 255, 255, 0.1);
						border       : 1px solid rgba(255, 255, 255, 0.2);
						border-radius: 5px;

						a {
							padding  : 0 5px;
							font-size: 12px;
							color    : $main-color-1;
						}
					}
				}
			}
		}

		.slider-ProductByCatalog {
			position: relative;
			margin  : 30px 30px 0 30px;
			overflow: hidden;

			figure {
				background: $main-color-4;

				.box-img {
					position: relative;
					height  : 180px;
					overflow: hidden;

					img {
						transition: .5s all ease-in-out;
					}

					&::after {
						content   : '';
						position  : absolute;
						top       : 0;
						left      : 0;
						width     : 100%;
						height    : 100%;
						transition: .3s all ease-in-out;
						background: #00000075;
					}
				}

				figcaption {
					padding: 10px;
					color  : $main-color-3;

					h3 {
						font-size    : 16px;
						font-weight  : 400;
						margin-bottom: 10px;
						color        : $main-color-1;

						overflow          : hidden;
						display           : -webkit-box;
						text-overflow     : ellipsis;
						-webkit-line-clamp: 1;
						-webkit-box-orient: vertical;
					}

					.info-event {
						display        : flex;
						justify-content: space-between;

						h5 {
							font-size: 13px;

							span {
								margin-left: 10px;
							}
						}
					}

				}

				&:hover {
					.box-img {
						img {
							transform: scale(1.1);
						}

						&::after {
							background: none;
						}
					}
				}
			}

			.swiper-button-prev {
				background-image: url(../img/icons/prev-slider.svg);
				background-size : cover;
				transition      : .2s all ease-in-out;
			}

			.swiper-button-next {
				background-image: url(../img/icons/next-slider.svg);
				background-size : cover;
				transition      : .2s all ease-in-out;
			}

			.swiper-button-next,
			.swiper-button-prev {
				&:hover {
					transform: scale(1.2);
				}
			}
		}
	}
}